container,
.header__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 150px;
}
@media (max-width: 1200px) {
  container,
  .header__wrapper {
    max-width: 1199px;
    padding: 0 30px;
  }
}
@media (max-width: 768px) {
  container,
  .header__wrapper {
    max-width: 767px;
    padding: 0 20px;
  }
}
.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-height: 56px;
}
.header__list {
  outline: 1px solid #f00;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  list-style: none;
  margin: 0;
  padding: 0;
  flex-grow: 1;
}
.header__item {
  width: 155px;
  margin-right: 30px;
}
.header__item:nth-child(3n) {
  margin-right: 0;
  padding-right: 0;
}
.header__item-icon {
  fill: #f00;
}
.header__item-icon--phone {
  margin-right: 4px;
}
.header__item-icon--mail {
  margin-right: 10px;
}
.header__item-icon--tg {
  margin-right: 8px;
}
.header__link {
  font-family: GothamPro_Regular;
  font-size: 12px;
  line-height: 20px;
  display: flex;
  align-items: center;
  text-decoration: none;
  letter-spacing: 0.0025em;
  color: #8391b2;
}
@media (max-width: 1200px) {
  .header__logo {
    width: 82px;
    height: 18px;
  }
  .header__item {
    width: auto;
    margin-right: 35px;
  }
  .header__item span {
    display: none;
  }
  .header__item svg {
    margin-right: 0;
  }
}
@media (max-width: 768px) {
  .header__logo {
    width: 52px;
    height: 11px;
  }
  .header__item-icon--phone {
    width: 8px;
    height: 8.67px;
  }
  .header__item-icon--mail {
    width: 9px;
    height: 6.75px;
  }
  .header__item-icon--tg {
    width: 8px;
    height: 8px;
  }
}
